:root { 
  --black: #000000;
  --white: #ffffff;
 
  --font-size-xl: 24px;
 
  --font-family-univers_next_pro-regular: "Univers Next Pro-Regular", Helvetica;
  --font-family-univers_next_pro-thin: "Univers Next Pro-Thin", Helvetica;
}
.universnextpro-thin-black-24px {
  color: var(--black);
  font-family: var(--font-family-univers_next_pro-thin);
  font-size: var(--font-size-xl);
  font-style: normal;
  font-weight: 100;
}
